@font-face {
  font-family: 'Anton';
  src: url(../fonts/Anton.ttf);
}
@font-face {
  font-family: 'CormorantInfantMediumItalic';
  src: url(../fonts/CormorantInfantMediumItalic.ttf);
}
@font-face {
  font-family: 'Roboto';
  src: url(../fonts/Roboto.ttf);
}
@font-face {
  font-family: 'ShareBold';
  src: url(../fonts/ShareBold.ttf);
}
@font-face {
  font-family: 'ShareTechMono';
  src: url(../fonts/ShareTechMono.ttf);
}

@function min2($numbers...) {
  @return m#{i}n(#{$numbers});
}

html,
body {
  font-size: 14px;
}
body {
  position: relative;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  z-index: 0;
  background-color: #ffffff;

  #root {
  }
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#card-generator-root {
  margin: 2rem;

  &.cs {
    margin: 0;
    .card-generator {
      margin: -6px 0 0 1px;
    }
  }

  &.full {
    margin: 0;
    .card-generator {
      max-width: 100vw;
      height: 100vh;
    }
  }
}

.card-generator {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 1600px;
  height: calc(100vh - 4rem);
  background-color: #f6fcfc;
  box-shadow: 3px 3px 9px 3px rgba(0, 0, 0, 0.2);

  .konvajs-content {
    width: auto !important;
    height: auto !important;
    canvas {
      position: relative !important;
      width: 100% !important;
      max-width: 825px !important;
      height: auto !important;
      background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQAQMAAAAlPW0iAAAABlBMVEX////MzMw46qqDAAAAEElEQVQImWNg+M+AFeEQBgB+vw/xfUUZkgAAAABJRU5ErkJggg==') !important;
    }
  }

  &__title {
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    background-color: #f0f0f0;
    box-shadow: inset 0px -6px 12px -6px rgba(0, 0, 0, 0.2);

    > h1 {
      flex: 0 1 auto;
      white-space: nowrap;
      font-family: 'ShareBold';
      font-size: 2rem;
      margin: 0;
    }
    > .input-group {
      flex: 0 1 40%;
      margin-left: 1rem;
      .input-group-text {
        font-weight: bold;
      }
    }
  }

  &__wrapper {
    display: flex;
    overflow: auto;
  }

  &__stage {
    flex: 0 1 min2(calc((100vh - 8rem) * 0.73333333), calc(825px + 2rem));
    padding: 1rem;
  }

  &__form {
    flex: 1 0 40%;
    padding: 1rem;
    overflow: auto;

    .form-group {
      padding-left: 0.5rem;
      border-left: 4px solid #495057;

      > label:first-child {
        font-weight: 600;
        display: flex;
        color: #495057;
        // > * {
        //   flex: 1 1 auto;
        // }
        // &:before,
        // &:after {
        //   content: '';
        //   position: relative;
        //   border-top: 2px solid #495057;
        //   top: 0.75rem;
        // }
        // &:before {
        //   flex: 0 1.5rem;
        //   margin-right: 0.5rem;
        // }
        // &:after {
        //   flex: 1 0 auto;
        //   margin-left: 0.5rem;
        // }
      }
      > :not(:last-child) {
        margin-bottom: 0.25rem;
      }

      .input-group-text {
        font-size: 0.875rem;
        padding: 0.375rem;
      }
    }
  }
}

@media (max-width: 991px) {
  #card-generator-root {
    margin: 1rem;
  }

  .card-generator {
    height: auto;
    max-width: calc(825px + 2rem);

    &__title {
      flex-direction: column;
      > h1 {
        margin-bottom: 0.5rem;
      }
      > .input-group {
        margin-left: 0;
      }
    }
    &__wrapper {
      flex-direction: column;
    }
    &__stage {
      flex: 1 0 auto;
    }
  }
}
